import React, { useState } from "react";
import Navbar from "../components/Navbar";

export default function CompoundInterest(props) {
  const [amount, setAmount] = useState(1000000)
  const [days, setDays] = useState(30)
  const [rate, setRate] = useState(55)

  const handleAmountChange = e => {
    const value = parseFloat(e.target.value.replace("$", ""))
    setAmount(isNaN(value) ? 0 : value)
  }
  const handleDaysChange = e => {
    const value = parseInt(e.target.value)
    setDays(isNaN(value) ? 1 : value)
  }
  const handleRateChange = e => {
    setRate(e.target.value)
  }

  console.log(days);
  return (
    <div className="compound-interest">
      <Navbar onLanguageClick={props.onLanguageClick} language={props.language} strings={props.strings} />
      <div className="compound--input-container">
        <label htmlFor="monto">Monto ARS</label>
        <input id="monto" type="text" value={`$${amount}`} onChange={handleAmountChange} />
      </div>
      <div className="compound--input-container">
        <label htmlFor="plazo">Plazo (días)</label>
        <input id="plazo" type="number" value={days} onChange={handleDaysChange}/>
      </div>
      <div className="compound--input-container">
        <label htmlFor="tasa">Tasa de interés</label>
        <input type="text" value={`${rate}%`} readOnly />
        <input id="tasa" type="range" min={0} max={100} value={rate} onChange={handleRateChange} />
      </div>
    </div>
  )
}